import { FormsModule } from '@angular/forms';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TranslateModule } from '@ngx-translate/core'; 
import { RouterModule, Routes } from '@angular/router';
import { OnlinePaymentComponent } from './online-payment.component';
import { PackageComponent } from './component/package/package.component';
import { ExtendedFeaturesComponent } from './component/extended-features/extended-features.component';
import { CardComponent } from './component/card/card.component';
import { PaymentDetailComponent } from './payment-detail/payment-detail.component';
import { NzTableModule } from 'ng-zorro-antd/table';
import { NzSelectModule } from 'ng-zorro-antd/select';
import { PopupPackageComponent } from './component/popup-package/popup-package.component';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { QrBankingModalComponent } from './component/qr-banking-modal/qr-banking-modal.component';
const routes: Routes = [
  {
    path: '',
    component: OnlinePaymentComponent
  },
  {
    path:'detail',
    component: PaymentDetailComponent
  }
]


@NgModule({
  declarations: [OnlinePaymentComponent, PackageComponent, ExtendedFeaturesComponent, CardComponent, PaymentDetailComponent, PopupPackageComponent,QrBankingModalComponent],
  imports: [
    CommonModule,
    TranslateModule,
    NgZorroAntModule,
    NzTableModule,
    NzSelectModule,
    RouterModule.forChild(routes),
    FormsModule
  ]
})
export class OnlinePaymentModule { }
